
import * as React from "react"
import invert from 'invert-color';

function PinKey({x, y}) {
  
  return(
      <>
        <rect x={x} y={y} width={265} height={280} fill="white" style={{stroke:"var(--bgColor1)", strokeWidth:3}} />
        <rect x={x} y={y} width={266} height={50} fill="var(--bgColor1)" />
        <text x={x + 55} y={y +38} fill="var(--fontColor1)" style={{fontSize:"2.5em", fontWeight:"bold"}}>Pin Status</text>
        <circle cx={x + 30} cy={y + 90} r={20} style={{stroke:"black", strokeWidth:3}} fill="red">
          <title>
            Digital High
          </title>  
        </circle>
        <text x={x + 60} y={y + 100} fill="black" style={{fontSize:"2em", fontWeight:"bolder"}}>Digital High</text>

        <circle cx={x + 30} cy={y + 140} r={20} style={{stroke:"black", strokeWidth:3}} fill="yellow">
          <title>
            Digital High-In
          </title>  
        </circle>
        <text x={x + 60} y={y + 150} fill="black" style={{fontSize:"2em", fontWeight:"bolder"}}>Digital High-In</text>

        <circle cx={x + 30} cy={y + 190} r={20} style={{stroke:"black", strokeWidth:3}} fill="teal">
          <title>
            Pwm On
          </title>  
        </circle>
        <text x={x + 60} y={y + 200} fill="black" style={{fontSize:"2em", fontWeight:"bolder"}}>Pwm On</text>
        
        <circle cx={x + 30} cy={y + 240} r={20} style={{stroke:"black", strokeWidth:3}} fill="black">
          <title>
            Disabled
          </title>  
        </circle>
        <text x={x + 60} y={y + 250} fill="black" style={{fontSize:"2em", fontWeight:"bolder"}}>Disabled</text>
      </>
    )
}

export default PinKey