
import * as React from "react"

function Sound({x, y, reading}) {

  let display = <>
                 <rect fill="#000000" style={{fillOpacity:0.75}} x={5} y={5} width={470} height={105} />
                 <text fill="white" x={320} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{"NA"}</text>
                </>;
  if(reading.hasOwnProperty("decibel"))
  {
    try{
      if(reading.decibel !== undefined)
      {
        display = <> 
                  <text fill="black" x={290} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{reading.decibel.toFixed(2)}</text>
                  <text fill="black" x={400} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>db</text>
                </>;
      }
    }
    catch(e)
    {
      console.error(e);
    }
  }
  return(
      <>
      {/* Decibels */}
        <rect width={480} height={115} fill="var(--bgColor2)" x={x} y={y}></rect>
        <rect fill="#FFFFFF"  x={x + 5} y={y+ 5} width={470} height={105} />
        <rect fill="var(--bgColor2)" width={265} height={115} x={x} y={y}/>
        <text  x={x + 32.5} y={y + 82.5}  fill= "var(--fontColor2)" style={{fontFamily: "Roboto" ,fontWeight:"bolder", fontSize: "5em" }}>Sound</text>
        {display}
      </>
    )
}

export default Sound