
import React, { useState } from "react"
import Piano from "./Piano.js"
function Tone({x, y, update}) {
//  const [freq, setFreq] = useState(null);
//  const [time, setTime] = useState(null);

      return(
      <>
        <rect width={1440} height={115} fill="var(--bgColor2)" x={x} y={y}></rect>
        <text  x={x + 57.5} y={y + 82.5}  fill= "var(--fontColor2)" style={{fontFamily: "Roboto" ,fontWeight:"bolder", fontSize: "5em" }}>Tone</text>
        <text  x={x+275} y={y +22.5}  fill= "var(--fontColor2)" style={{fontWeight:"bold", fontSize: "1.25em" }}>Frequency</text>            
        <foreignObject x={x+275} y={y +30} height={25} width={100}                                                                                                                                                                                         >
          <input type="number" id="tone_freq" name="frequency" min="0" max="65,535"  style={{width:"90%"}} />
        </foreignObject>

        <text  x={x+275} y={y +75}  fill= "var(--fontColor2)" style={{fontWeight:"bold", fontSize: "1.25em" }}>Time</text>            
        <foreignObject x={x+275} y={y +80} height={25} width={100}                                                                                                                                                                                         >
          <input type="number" id="tone_time" name="time" min="0" max="65,535" style={{width:"90%"}} />
        </foreignObject>

        <foreignObject x={x+400} y={y+5} height={105} width={95}>
          <button onClick={() => {update("TONE", {freq: document.getElementById("tone_freq").value, time: document.getElementById("tone_time").value}) }} style={{width:"100%", height:"100%"}}>Play</button>
        </foreignObject>
        <Piano x={x+ 500} y={y + 5} update={update} />
      </>
    )
   
}

export default Tone