
import React, { useState } from "react";
import invert from 'invert-color';

function PwmPin({id, x, y, update, value}) {

  const [pwmVal, setPwm] = useState(value);
  let pinValue = ""; 
  if(id !== "NA" && id !== "START" &&  id !== undefined)
  {
    let pwmDigits = pwmVal.toString().length;
    pinValue = <>
              <text  x={120} y={22.5}  fill= "#121212" style={{fontWeight:"bold", fontSize: "1.25em" }}>HIGH</text>
              <line x1="168" y1="16.5" x2="460" y2="16.5" stroke="#121212" style={{strokeWidth:"3"}}strokeDasharray="4" />  
              <text  x={120} y={60}  fill= "#121212" style={{fontWeight:"bold", fontSize: "1.25em" }}>LOW</text>  
              <line x1="168" y1="53.5" x2="460" y2="53.5" stroke="#121212" style={{strokeWidth:"3"}}strokeDasharray="4" />  
              <rect fill="#121212" width={pwmVal *1.13725} height={3} x={168} y={15} />
              <rect fill="#121212" width={3} height={40} x={168 + (pwmVal * 1.13725)} y={15} />
              <rect fill="#121212" width={(289.99875000000003) - pwmVal *1.13725} height={3} x={169.5 + (pwmVal * 1.13725)} y={52} />
              
              <foreignObject x={160} y={60} height={20} width={311.22448}                                                                                                                                                                                         >
                <input type="range" id="pwm" name="pwm" min="0" max="255" step="1" onChange ={e => {setPwm(e.target.value)}} value={pwmVal} style={{width:"98%"}} />
              </foreignObject>

              <foreignObject x={165} y={73} height={34} width={42}>
                <button class="btn btn-sm color4" onClick={() => { update("PWM", {pin: id, value: "0"}); setPwm(0);}}>Off</button>
              </foreignObject>

              <foreignObject x={224} y={73} height={34} width={26}>
                <button class="btn btn-sm color4" onClick={() => {pwmVal > 0 ? setPwm(pwmVal - 1) : setPwm(0) }}>-</button>
              </foreignObject>
              <rect fill="white" style={{stroke:"#121212", strokeWidth: "3"}} width={55} height={30} x={255} y={75}/>
              <text  x={290 - (15 * (pwmDigits - 1))} y={100}  fill= "#121212" style={{fontWeight:"bold", fontSize: "1.9em" }}>{pwmVal}</text>   

              <foreignObject x={321} y={73} height={34} width={29}>
                <button class="btn btn-sm color4" onClick={() => { pwmVal < 255 ? setPwm(parseInt(pwmVal) + 1) : setPwm(255)}}>+</button>
              </foreignObject>

              <foreignObject x={371} y={73} height={34} width={77}>
                <button class="btn btn-sm color4" onClick={() => update("PWM", {pin: id, value: pwmVal})}>Update</button>
              </foreignObject>
            </>;
  }
  else if(id === "NA")
  {//PWM not available
    pinValue = <>
                  <rect fill="#000000" style={{fillOpacity:0.75}} x={5} y={5} width={470} height={105} />
                  <text fill="white" x={160} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{"Not Available"}</text>
                </>;
  }

 //update(1, value + 1)
    return(
      <>
      <svg width={480} height={115} fill="none" x={x} y={y}>
          <rect fill="var(--bgColor4)"  width={480} height={115} />
          <rect fill="white"  x={5} y={5} width={470} height={105} />
          <rect fill="var(--bgColor4)" width={110} height={115} x={0} y={0}/>
          <text  x={40} y={65}  fill= "var(--fontColor4)" style={{fontFamily: "Roboto" ,fontWeight:"bolder", fontSize: "5em" }}>~</text>
          <text  x={25} y={90}  fill= "var(--fontColor4)" style={{fontWeight:"bold", fontSize: "1.9em" }}>PWM</text>   
          {pinValue}
      </svg>
      </>
    )
   
}

export default PwmPin