
import React, { useState } from "react"
import { CompactPicker } from 'react-color';
function NeoPixel({x, y, pixel, update}) {
  const [mainColor, setMainColor] = useState("#FFFFFF");
    function updateColor(color){
        document.getElementById("neo_color").setAttribute("fill", "rgb(" + color.red + "," +  color.green + "," +  color.blue + ")");
        setMainColor(color);
        console.log(color);
        document.getElementById("border_" + pixel).setAttribute("fill", "rgb(" + color.rgb.r + "," +  color.rgb.g + "," +  color.rgb.b + ")");
        update("NEO", {pixel: pixel, red: color.rgb.r, green: color.rgb.g, blue: color.rgb.b});
    }
      return(
      <>
        <rect width={480} height={115} fill="var(--bgColor2)" x={x} y={y}></rect>
        <rect fill="#FFFFFF"  x={x + 500} y={y+ 10} width={440} height={95} />
        <rect fill={mainColor.hex} stroke="black" strokeWidth="3" id="neo_color" width={150} height={75} x={x+778} y={y+21}/>
        <text  x={x + 80} y={y + 82.5}  fill= "var(--fontColor2)" style={{fontFamily: "Roboto" ,fontWeight:"bolder", fontSize: "5em" }}>NeoPixel {pixel}</text>
        <foreignObject x={x+518} y={y+12.5} height={95} width={248}>
           <CompactPicker color={mainColor} onChange={updateColor} />
        </foreignObject>
      </>
    )
   
}

export default NeoPixel