
import * as React from "react"

function Motion({x, y, reading}) {

  let motion = <>
                 <rect fill="#000000" style={{fillOpacity:0.75}} x={5} y={5} width={470} height={105} />
                 <text fill="white" x={160} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{"Not Available"}</text>
                 <rect fill="#000000" style={{fillOpacity:0.75}} x={485} y={5} width={470} height={105} />
                 <text fill="white" x={640} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{"Not Available"}</text>
                 <rect fill="#000000" style={{fillOpacity:0.75}} x={965} y={5} width={470} height={105} />
                 <text fill="white" x={1120} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{"Not Available"}</text>
                </>;
  if(reading !== "NA" && reading !== "START")
  {
        motion = <> 
                 <text fill="black" x={160} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{reading.X}</text>
                 <text fill="black" x={640} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{reading.Y}</text>
                 <text fill="black" x={1120} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{reading.Z}</text>
                </>;
  }
  return(
      <>
      {/* X */}
        <rect width={480} height={115} fill="var(--bgColor2)" x={x} y={y}></rect>
        <rect fill="#FFFFFF"  x={x + 5} y={y+ 5} width={470} height={105} />
        <rect fill="var(--bgColor2)" width={110} height={115} x={x} y={y}/>
        <text  x={x + 32.5} y={y + 82.5}  fill= "var(--fontColor2)" style={{fontFamily: "Roboto" ,fontWeight:"bolder", fontSize: "5em" }}>X</text>

      {/* Y */}
        <rect width={480} height={115} fill="var(--bgColor3)" x={x + 480} y={y}></rect>
        <rect fill="#FFFFFF"  x={x + 485} y={y+ 5} width={470} height={105} />
        <rect fill="var(--bgColor3)" width={110} height={115} x={x + 480} y={y}/>
        <text  x={x + 512.5} y={y + 82.5}  fill= "var(--fontColor3)" style={{fontFamily: "Roboto" ,fontWeight:"bolder", fontSize: "5em" }}>Y</text>
      {/* Z */}
        <rect width={480} height={115} fill="var(--bgColor4)" x={x + 960} y={y}></rect>
        <rect fill="#FFFFFF"  x={x + 965} y={y+ 5} width={470} height={105} />
        <rect fill="var(--bgColor4)" width={110} height={115} x={x + 960} y={y}/>
        <text  x={x + 992.5} y={y + 82.5}  fill= "var(--fontColor4)" style={{fontFamily: "Roboto" ,fontWeight:"bolder", fontSize: "5em" }}>Z</text>
        {motion}
      </>
    )
}

export default Motion
