
import * as React from "react"

function Temperature({x, y, reading}) {

  let temp = <>
              <text x={x + 135} y={y + 35} fill="white" style={{fontSize:"2.5em"}}>Temperature</text>
              <text x={x + 90} y={y + 90} fill="Black" style={{fontSize:"2em"}}>No Reading Available</text>
            </>;
  if(reading !== "NA" && reading !== "START")
  {
    //Code taken from Adafruit Circuit Playground
    const SERIESRESISTOR = 10000; ///< series resistor for thermistor
    const THERMISTORNOMINAL = 10000; ///< resistance of thermistor at 25 degrees C
    const TEMPERATURENOMINAL =25; ///< temp. for nominal resistance (almost always 25 C)
    const BCOEFFICIENT = 3380; ///< The beta coefficient of the thermistor (usually 3000-4000) 
    reading = ((1023.0 * SERIESRESISTOR) / reading);
    reading -= SERIESRESISTOR;
    let steinhart = reading / THERMISTORNOMINAL;     // (R/Ro)
    steinhart = Math.log(steinhart);                  // ln(R/Ro)
    steinhart /= BCOEFFICIENT;                   // 1/B * ln(R/Ro)
    steinhart += 1.0 / (TEMPERATURENOMINAL + 273.15); // + (1/To)
    steinhart = 1.0 / steinhart;                 // Invert
    let celsius = steinhart - 273.15;
    let farenheight = celsius * 1.8 + 32;

    temp = <> 
            <text x={x + 85} y={y + 35} fill="var(--fontColor3)" style={{fontSize:"2em"}}>Celsius</text> 
            <text x={x + 92.5} y={y + 90} fill="Black" style={{fontSize:"2em"}}>{celsius.toFixed(2)}&#176;</text>
            
            <text x={x + 250} y={y + 35} fill="var(--fontColor3)" style={{fontSize:"2em"}}>Farenheight</text>
            <text x={x + 290} y={y + 90} fill="Black" style={{fontSize:"2em"}}> {farenheight.toFixed(2)}&#176;</text> 
          </>;
  }
  return(
      <>
        <rect x={x} y={y} width={480} height={112.5} fill="white" style={{stroke:"var(--bgColor3)", strokeWidth:5}} />
        <rect x={x} y={y} width={480} height={50} fill="var(--bgColor3)" />
        {temp}
      </>
    )
}

export default Temperature
