
import  React, {useState} from "react";

function DigitalPin({id, x, y, value=[0,0], pwmActive, update}) {

  let pinId = <text  x={25} y={100}  fill= "var(--fontColor3)" style={{fontWeight:"bold", fontSize: "3em" }}>{id}</text>;
  let pinValue = "";
  let pinMode = "";
  if(id === "NA" || id === "START" || id === undefined)
  {
    pinId = <text  x={15} y={90}  fill= "var(--fontColor3)" style={{fontWeight:"bold", fontSize: "1.9em" }}>Digital</text>;
    if(id === "NA")
    {//Analog values are not available
      pinValue = <>
                  <rect fill="#000000" style={{fillOpacity:0.75}} x={5} y={5} width={470} height={105} />
                  <text fill="white" x={160} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{"Not Available"}</text>
                </>;
    }
  }
  else
  {
    if(pwmActive)
    {
      
      pinValue = <>
                  <rect fill="#000000" style={{fillOpacity:0.75}} x={5} y={5} width={470} height={105} />
                  <text fill="white" x={170} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{"Turn off PWM"}</text>
                </>;

    }
    else
    {
      switch(value[0])
      {
        case 0://LOW
        if(value[1] == 1)
        {//Only allow for setting High/Low when the pin is set to output
            pinValue = <>
                        <text  x= {157.5} y={55}  fill="black" style={{fontWeight: "bold", fontSize: "4em"}}>0</text> 
                        <text  x= {130} y={100}  fill="black" style={{fontWeight: "bold", fontSize: "3.5em"}}>LOW</text>
                        <rect fill="black"  x={214} y={15} width={4} height={25} />
                        <rect fill="black"  x={203} y={40} width={25} height={3} />
                        <rect fill="black"  x={208} y={45} width={15} height={3} />
                        <rect fill="black"  x={213} y={50} width={5} height={3} />
                        <circle fill="white" style={{cursor:'pointer', stroke:"black", strokeWidth:2}} class="clickable" onClick={() => update("DIGITAL", {pin: id, value: 1})} cx={260} cy={37.5} r={15}>
                          <title>HIGH</title>
                        </circle>
                        <circle fill="black" style={{ stroke:"black", strokeWidth:2}} class="clickable" cx={260} cy={82.5} r={15}>
                          <title>LOW</title>
                        </circle>
                      </>;
          } 
          else
          {
            pinValue = <> 
                        <text  x= {177.5} y={55}  fill="black" style={{fontWeight: "bold", fontSize: "4em"}}>0</text> 
                        <text  x= {150} y={100}  fill="black" style={{fontWeight: "bold", fontSize: "3.5em"}}>LOW</text>
                        <rect fill="black"  x={234} y={15} width={4} height={25} />
                        <rect fill="black"  x={223} y={40} width={25} height={3} />
                        <rect fill="black"  x={228} y={45} width={15} height={3} />
                        <rect fill="black"  x={233} y={50} width={5} height={3} />
                      </>;
          }   
          break;
        case 1://HIGH
          if(value[1] == 1)
          {//Only allow for setting High/Low when the pin is set to output
              pinValue = <>
                          <text  x= {155} y={55}  fill="red" style={{fontWeight: "bold", fontSize: "4em"}}>1</text> 
                          <text  x= {120} y={100}  fill="red" style={{fontWeight: "bold", fontSize: "3.5em"}}>HIGH</text>
                          <rect fill="red"  x={204} y={15.5} width={4} height={10.5} />
                          <rect fill="red"  x={193} y={26} width={25} height={4} />
                          <rect fill="red"  x={198} y={31.5} width={15} height={4} />
                          <rect fill="red"  x={193} y={37.5} width={25} height={4} />
                          <rect fill="red"  x={198} y={43.5} width={15} height={4} />
                          <rect fill="red"  x={204} y={45} width={4} height={10} />
                          <circle fill="black" style={{stroke:"black", strokeWidth:2}} class="clickable" cx={260} cy={37.5} r={15}>
                            <title>HIGH</title>
                          </circle>
                          <circle fill="white" style={{cursor:'pointer', stroke:"black", strokeWidth:2}} class="clickable" onClick={() => update("DIGITAL", {pin: id, value: 0})} cx={260} cy={82.5} r={15}>
                            <title>LOW</title>
                          </circle>
                        </>;
          }
          else
          {
            pinValue = <>
                        <text  x= {175} y={55}  fill="red" style={{fontWeight: "bold", fontSize: "4em"}}>1</text> 
                        <text  x= {140} y={100}  fill="red" style={{fontWeight: "bold", fontSize: "3.5em"}}>HIGH</text>
                        <rect fill="red"  x={224} y={15.5} width={4} height={10.5} />
                        <rect fill="red"  x={213} y={26} width={25} height={4} />
                        <rect fill="red"  x={218} y={31.5} width={15} height={4} />
                        <rect fill="red"  x={213} y={37.5} width={25} height={4} />
                        <rect fill="red"  x={218} y={43.5} width={15} height={4} />
                        <rect fill="red"  x={224} y={45} width={4} height={10} />
                      </>;
          }
          break;
        case 2://HIGH-IN
          pinValue = <>
                      <text  x= {175} y={55}  fill="#EBD300" style={{fontWeight: "bold", fontSize: "4em"}}>1</text> 
                      <text  x= {116} y={100}  fill="#EBD300" style={{fontWeight: "bold", fontSize: "3em"}}>HIGH-IN</text>
                      <polygon points="225,15 210,35 225,35" fill="#EBD300"/>
                      <polygon points="217.5,34 232.5,34 217.5,55" fill="#EBD300"/>
                    </>;
          break;

      }
      let pinHeader = <>
                          <rect fill="var(--bgColor3)" width={5} height={115} x={286} y={0}/>
                          <rect fill="var(--bgColor3)" width={194} height={30} x={290} y={4}/>
                          <text  x= {325} y={27}  fill="var(--fontColor3)" style={{fontWeight: "bold", fontSize: "2em"}}>PinMode</text>
                        </>;
      switch(value[1])
      {
        
        case 0://INPUT
          pinMode = <>
                      {pinHeader}
                      <text  x= {325} y={85}  fill="black" style={{fontWeight: "bold", fontSize: "2.5em"}}>INPUT</text>
                      <circle fill="black" style={{stroke:"black", strokeWidth:2}} class="clickable" cx={450} cy={53} r={7}>
                        <title>Input</title>
                      </circle>
                      <circle fill="white" style={{cursor:'pointer', stroke:"black", strokeWidth:2}} class="clickable" onClick={() => update("PINMODE", {pin: id, value: 2})} cx={450} cy={72} r={7}>
                        <title>Input Pullup</title>
                      </circle>
                      <circle fill="white" style={{cursor:'pointer', stroke:"black", strokeWidth:2}} class="clickable" onClick={() => update("PINMODE", {pin: id, value: 1})} cx={450} cy={91} r={7}>
                        <title>Output</title>
                      </circle>
                    </>;
                    
          break;
        case 1://OUTPUT
          pinMode = <>
                      {pinHeader}
                      <text  x= {305} y={85}  fill="black" style={{fontWeight: "bold", fontSize: "2.5em"}}>OUTPUT</text>
                      <circle fill="white" style={{cursor:'pointer', stroke:"black", strokeWidth:2}} class="clickable" onClick={() => update("PINMODE", {pin: id, value: 0})} cx={450} cy={53} r={7}>
                        <title>Input</title>
                      </circle>
                      <circle fill="white" style={{cursor:'pointer', stroke:"black", strokeWidth:2}} class="clickable" onClick={() => update("PINMODE", {pin: id, value: 2})} cx={450} cy={72} r={7}>
                        <title>Input Pullup</title>
                      </circle>
                      <circle fill="black" style={{stroke:"black", strokeWidth:2}} class="clickable" cx={450} cy={91} r={7}>
                        <title>Output</title>
                      </circle>
                    </>;
          break;
        case 2://INPUT_PULLUP
          pinMode = <>
                      {pinHeader}
                      <text  x= {320} y={70}  fill="black" style={{fontWeight: "bold", fontSize: "2.5em"}}>INPUT</text>
                      <text  x= {310} y={100}  fill="black" style={{fontWeight: "bold", fontSize: "2.5em"}}>PULLUP</text>
                      <circle fill="white" style={{cursor:'pointer', stroke:"black", strokeWidth:2}} class="clickable" onClick={() => update("PINMODE", {pin: id, value: 0})} cx={450} cy={53} r={7}>
                        <title>Input</title>
                      </circle>
                      <circle fill="black" style={{ stroke:"black", strokeWidth:2}} class="clickable" cx={450} cy={72} r={7}>
                        <title>Input Pullup</title>
                      </circle>
                      <circle fill="white" style={{cursor:'pointer', stroke:"black", strokeWidth:2}} class="clickable" onClick={() => update("PINMODE", {pin: id, value: 1})} cx={450} cy={91} r={7}>
                        <title>Output</title>
                      </circle>
                    </>;
          break;

      }
      
    }
    
  }
    return(
      <>
      <svg width={480} height={115} fill="none" x={x} y={y}>
          <rect fill="var(--bgColor3)"  width={480} height={115} />
          <rect fill="#FFFFFF"  x={5} y={5} width={470} height={105} />
          <rect fill="var(--bgColor3)" width={110} height={115} x={0} y={0}/>
          <text  x={40} y={65}  fill= "var(--fontColor3)" style={{fontFamily: "Roboto" ,fontWeight:"bolder", fontSize: "5em" }}>#</text>
          {pinId}
          {pinValue}
          {pinMode}
      </svg>
      </>
    )

}

export default DigitalPin