
import React, { useState } from "react"

function Piano({x, y, update}) {
    let octaves = new Array();
    let frequencies = [33, 35, 37, 39, 41, 44, 46, 49, 52, 55, 58,62,65,69,73,78,82,87,93,98,104,110,117,123,131,139,147,156,165,175,185,196,208,220,233,247,262,277,294,311,330,349,370,392,415,550,466,494,523,554,587,622,659,698,740,784,831,880,932,988,1047,1109,1175,1245,1319,1397,1480,1568,1661,1760,1865,1976,2093,2217,2349,2489,2637,2794,2960,3136,3322,3520,3729,3951,4186,4435,4699,4978];
    for(let i = 0; i < 7; i++)
    {
        octaves[i]= <>
                    <rect width={15} height ={105} x={0 + (105 * i)} y={0} fill="white" stroke="black" className="pianoKey"  strokeWidth={1} onClick={() => {update("TONE", {freq: frequencies[i * 12] , time: document.getElementById("tone_time").value}) }} >
                        <title>C</title>
                    </rect>
                    <rect width={15} height ={105} x={15 + (105 * i)} y={0} fill="white" stroke="black" className="pianoKey" strokeWidth={1}  onClick={() => {update("TONE", {freq: frequencies[2 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>D</title>
                    </rect>
                    <rect width={7.5} height ={50} x={11.25 + (105 * i)} y={0} fill="black" stroke="black" className="pianoSharpKey" strokeWidth={1}  onClick={() => {update("TONE", {freq: frequencies[1 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>C#</title>
                    </rect>
                    <rect width={15} height ={105} x={30 + (105 * i)} y={0} fill="white" stroke="black" className="pianoKey"  strokeWidth={1} onClick={() => {update("TONE", {freq:frequencies[4 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>E</title>
                    </rect>
                    <rect width={7.5} height ={50} x={26.25 + (105 * i)} y={0} fill="black" stroke="black" className="pianoSharpKey" strokeWidth={1}  onClick={() => {update("TONE", {freq: frequencies[3 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>D#</title>
                    </rect>
                    <rect width={15} height ={105} x={45 + (105 * i)} y={0} fill="white" stroke="black" className="pianoKey" strokeWidth={1}  onClick={() => {update("TONE", {freq:frequencies[5 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>F</title>
                    </rect>
                    <rect width={15} height ={105} x={60 + (105 * i)} y={0} fill="white" stroke="black" className="pianoKey" strokeWidth={1}  onClick={() => {update("TONE", {freq:frequencies[7 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>G</title>
                    </rect>
                    <rect width={7.5} height ={50} x={56.25 + (105 * i)} y={0} fill="black" stroke="black" className="pianoSharpKey" strokeWidth={1}  onClick={() => {update("TONE", {freq:frequencies[6 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>F#</title>
                    </rect>
                    <rect width={15} height ={105} x={75 + (105 * i)} y={0} fill="white" stroke="black" className="pianoKey" strokeWidth={1}  onClick={() => {update("TONE", {freq:frequencies[9 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>A</title>
                    </rect>
                    <rect width={15} height ={105} x={90 + (105 * i)} y={0} fill="white" stroke="black" className="pianoKey" strokeWidth={1}  onClick={() => {update("TONE", {freq:frequencies[11 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>B</title>
                    </rect>
                    <rect width={7.5} height ={50} x={71.25 + (105 * i)} y={0} fill="black" stroke="black" className="pianoSharpKey" strokeWidth={1}  onClick={() => {update("TONE", {freq:frequencies[8 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>G#</title>
                    </rect>
                    <rect width={7.5} height ={50} x={86.25 + (105 * i)} y={0} fill="black" stroke="black" className="pianoSharpKey" strokeWidth={1}  onClick={() => {update("TONE", {freq:frequencies[10 + (i * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>A#</title>
                    </rect>
                </>;
    }
    octaves[7] = <>
                    <rect width={15} height ={105} x={0 + (105 * 7)} y={0} fill="white" stroke="black" className="pianoKey"  strokeWidth={1} onClick={() => {update("TONE", {freq: frequencies[7 * 12] , time: document.getElementById("tone_time").value}) }} >
                        <title>C</title>
                    </rect>
                    <rect width={20} height ={105} x={15 + (105 * 7)} y={0} fill="white" stroke="black" className="pianoKey" strokeWidth={1}  onClick={() => {update("TONE", {freq: frequencies[2 + (7 * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>D</title>
                    </rect>
                    <rect width={7.5} height ={50} x={11.25 + (105 * 7)} y={0} fill="black" stroke="black" className="pianoSharpKey" strokeWidth={1}  onClick={() => {update("TONE", {freq: frequencies[1 + (7 * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>C#</title>
                    </rect>
                    <rect width={7.5} height ={50} x={26.25 + (105 * 7)} y={0} fill="black" stroke="black" className="pianoSharpKey" strokeWidth={1}  onClick={() => {update("TONE", {freq: frequencies[3 + (7 * 12)], time: document.getElementById("tone_time").value}) }} >
                        <title>D#</title>
                    </rect>
                    
                </>;
      return(
      <>
        <svg width={800} height={105} viewBox="0 0 800 105" fill="none" x={x} y={y}>
           {octaves[0]}
           {octaves[1]}
           {octaves[2]}
           {octaves[3]}
           {octaves[4]}
           {octaves[5]}
           {octaves[6]}
           {octaves[7]}
        </svg>
      </>
    )
   
}

export default Piano