
import * as React from "react";
import invert from 'invert-color';

function AnalogPin({id, x, y, value=0, pwmActive}) {

  let pinValue = "";
  let pinId = <text  x={25} y={100}  fill= "var(--fontColor2)" style={{fontWeight:"bold", fontSize: "3em" }}>{id}</text>;
  if(id === "NA" || id === "START" || id === undefined)
  {
    pinId = <text  x={8} y={90}  fill= "var(--fontColor2)" style={{fontWeight:"bold", fontSize: "1.9em" }}>Analog</text>;
    if(id === "NA")
    {//Analog values are not available
      pinValue = <>
                  <rect fill="#000000" style={{fillOpacity:0.75}} x={5} y={5} width={470} height={105} />
                  <text fill="white" x={160} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{"Not Available"}</text>
                </>;
    }
  }
  else
  {
    if(pwmActive)
    {
      
      pinValue = <>
                  <rect fill="#000000" style={{fillOpacity:0.75}} x={5} y={5} width={470} height={105} />
                  <text fill="white" x={170} y={75} style={{fontWeight:"bolder", fontSize:"3em"}}>{"Turn off PWM"}</text>
                </>;

    }
    else
    {
      let valueSize = value.toString().length - 1;
      pinValue = <>
                  <text fill="black" x={280 - (valueSize * 12.5)} y={45} style={{fontWeight:"bolder", fontSize:"3em"}}>{value}</text>
                  <polygon points={(120 + 0.337243 * value) + ",68 " +  (109 + 0.337243 * value)  + ",50 " + (129 + 0.337243 * value)  + ",50" } fill="black"/>
                  <rect width={345} height={35} x={120} y={70} fill="url(#analogGradient)" style={{strokeWidth:2, stroke:"black"}} />
                </>;
    }
  }
    return(
      <>
      <svg width={480} height={115} fill="none" x={x} y={y}>
        <defs>
          <linearGradient id="analogGradient" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" style={{stopColor:'#E9E9E9', stopOpacity:1}} />
            <stop offset="100%" style={{stopColor:'#121212', stopOpacity:1}} />
          </linearGradient>
        </defs>
          <rect fill="var(--bgColor2)"  width={480} height={115} />
          <rect fill="#FFFFFF"  x={5} y={5} width={470} height={105} />
          <rect fill="var(--bgColor2)" width={110} height={115} x={0} y={0}/>
          <text  x={32.5} y={65}  fill= "var(--fontColor2)" style={{fontFamily: "Roboto" ,fontWeight:"bolder", fontSize: "5em" }}>∩</text>
          {pinId}
          {pinValue}
      </svg>
      </>
    )
}

export default AnalogPin